<template>
  <div class="container">
    <div class="top">
      <img :src="title_image">
      <img src="../../../assets/red.png">
      <p>{{headtext}}</p>
      <img class="red" src="../../../assets/red.png">
      <img class="post" :src="baseUrl+headlog">
      <img class="arrow" src="../../../assets/arrow.png">
    </div>
    <div class="content">
      <div class="list" v-for="(item,index) in list" :key="index">
        <p>{{item.title}}</p>
        <p>{{item.text}}</p>
        <div class="email clearfix">
          <img :src="baseUrl+item.log">
          <p>{{item.logtext}}</p>
        </div>
      </div>

      <div class="tip">
        <img src="../../../assets/warning.png">
        <p>如果您的建议或意见未能得到及时反馈，可以在《华联视线》微信服务号进行留言。我们愿意成为您的传话筒，并期待与您的更多合作。</p>
      </div>
    </div>
  </div>
</template>
<script>
  var config = require("./../../../assets/config.js");
  const getinfo = config.api.contactgetinfo;
  const projectname = config.projectname;
  const urls = config.urls;
  export default {
    data() {
      return {
        baseUrl: urls + "/" + projectname + "/",
       list:[],
       headlog:'',
       headtext:'',
       title_image:require("../../../assets/contact_title.png")
      }
    },
    mounted () {
      this.$http.get(getinfo).then(
        response => {
          console.log(response.body.info);
          this.headlog = response.body.info.headlog;
          this.headtext = response.body.info.headtext;
          this.list = JSON.parse(response.body.info.content);
        },
        error => {
          console.log(error);
        }
      )
    }
  }

</script>
<style scoped>
  .top {
    padding: 123px 82px 36px 82px;
    border-bottom: 1px solid #959595;
    text-align: center;
  }

  .top>img:nth-child(1) {
    width: 256px;
    height: 147px;
  }

  .top>img:nth-child(2) {
    display: block;
    width: 24px;
    height: 26px;
    margin: 37px auto;
  }

  .top>p {
    font-size: 24px;
    color: #313131;
    line-height: 44px;
  }

  .top>.red {
    display: block;
    width: 24px;
    height: 26px;
    margin: 0 auto;
    margin-top: 41px;
  }

  .top>.post {
    display: block;
    width: 220px;
    height: 380px;
    margin: 0 auto;
    margin-top: 57px;
  }

  .top>.arrow {
    display: block;
    width: 23px;
    height: 16px;
    margin: 0 auto;
    margin-top: 31px;
  }

  .content {
    padding: 65px 0 125px 0;
  }

  .content>.list {
    padding: 25px 80px;
    border-bottom: 1px solid #c8c8c8;
  }

  .content>div:nth-of-type(2n) {
    background: #eeeeee;
  }

  .list>p:nth-child(1) {
    padding-left: 15px;
    border-left: 10px solid #d20107;
    font-size: 28px;
    color: #d20107;
    font-weight: bold;
  }

  .list>p:nth-child(2) {
    margin-top: 20px;
    line-height: 50px;
    font-size: 22px;
    color: #7a7a7a;
  }

  .list>.email>img {
    width: 28px;
    height: 18px;
    float: left;
    margin: 10px 10px 0 0;
  }

  .list>.email>p {
    font-size: 24px;
    font-weight: bold;
    color: #edad94;
    float: left;
  }

  .tip {
    width: 460px;
    height: 140px;
    border: 5px solid #c9c9c9;
    margin: 90px auto;
    position: relative;
    padding: 13px 18px;
  }
  .tip>img{
      position: absolute;
      width:44px;
      height: 38px;
      top:-20px;
      left: 230px;
  }
  .tip>p{
      line-height: 40px;
      font-size: 20px;
      color: #4f4f4f;
      margin-top: 10px;
  }
</style>



