<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>
<script>
export default {
  name: "app"
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  background: #f2f4f3;
}
body{
  font-family: "Microsoft YaHei";
  margin: 0;
  padding: 0;
  background: #f2f4f3;
}
* {
  margin: 0;
  padding: 0;
}
.fl {
  float: left;
}

.fr {
  float: right;
}

.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}

.clearfix {
  zoom: 1;
}
a{
  text-decoration: none;  
}
img {
  vertical-align: middle;
  border: 0;
  outline: 0;
  max-width: 100%;
}
p {
  text-align: justify;
  text-justify: inter-word;
}
ul li,
ol li {
  list-style: none;
}
.banner{
  height: 350px;
}
.banner .is-active{
  background:#5260b3 !important;
  opacity: 1;
}
.banner .mint-swipe-indicator{
  width: 12px;
  height: 12px;
  border-radius:50%;
  opacity: 1; 
  background: #b3b3b3;
  margin-right: 10px !important;
}
.store_info .mint-tab-item-label{
  font-size: 28px;
}
.store_info .mint-tab-item-label{
  line-height: 1.5;
}
.store_info .mint-navbar{
  display: block;
  width: 56%;
  margin: 0 auto;
  background: none;
}
.store_info .mint-tab-item {
  width: 32%;
  padding: 5px 0 !important;
  margin-top: 20px;
  font-weight: 700;
}
.store_info .mint-tab-item:nth-of-type(1){
  float: left;
}
.store_info .mint-tab-item:nth-of-type(2){
  float: right;
}
.store_info .mint-tab-item.is-selected{
  color: #5e6dc5;
  border-bottom: 4px solid #5e6dc5;
  
}
.store_info .mint-loadmore-top{
  margin-top: 0;
}
</style>



