<template>
  <div class="details">
    <header>
      <p class="title">门店详情</p>
      <p><img class="logo" src="../../assets/detaillogo.png" alt=""></p>
      <p class="pass">{{details}}</p>
    </header>
    <div class="content">
      <div class="position">
        <img src="../../assets/position.png" alt="">
        <div class="name">
          <!-- <span class="sp1">BHG Mall</span> -->
          <span class="sp2">{{name}}</span>
        </div>
        <i>{{areaInfo}}</i>
      </div>
      <div class="introduct">
        <img class="icon" src="../../assets/icon1.png" alt="">
        <p class="intros">
          <span>简介/</span>
          <i>Introduction</i>
        </p>
        <p class="passage">{{introduction}}</p>
      </div>
      <div class="introduct">
        <img class="icon" src="../../assets/icon2.png" alt="">
        <p class="intros">
          <span>附近环境/</span>
          <i>The environment</i>
        </p>
        <p class="passage">{{enviroment}}
        </p>
      </div>
      <div class="introduct">
        <img class="icon" src="../../assets/icon3.png" alt="">
        <p class="intros title">
          <span>品牌/</span>
          <i>Brand</i>
        </p>
        <ul class="brand">
          <li v-for="(item,index) in brands" :key="index">
            <span class="title">{{item.title}} <img src="../../assets/st.png" class="nav-logo"></span>
            <div class="brandlogos">
              <div class="blogos clearfix" :class="item.showall? 'showall': 'showsome'">
                <img class="imgs fl" v-for="(items,index) in item.brandlogos" :key="index" :src="baseUrl + items.url" alt="">
              </div>
               <span v-if="item.con == '查看全部'" class="more" @click="lookore(index)">查看全部 <img src="../../assets/st.png" class="nav-logo"></span>
               <span v-if="item.con == '收起'" class="more" @click="hidemore(index)">收起 <img src="../../assets/st.png" class="nav-logo"></span>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <footer>
      <img class="endlogo" src="../../assets/endlogo.png" alt="">
      <div class="contact">
        <p>
          <span>详细地址: </span>
          <i>{{address}}</i>
        </p>
        <p>
          <span>招商电话: </span>
          <i>{{telephone}}</i>
        </p>
        <p>
          <span>市场合作咨询: </span>
          <i>{{consult}}</i>
        </p>
        <p>
          <span>营业时间: </span>
          <i>{{worktime}}</i>
        </p>
      </div>
      <div class="map">
        <p>[ 点击地图查看位置 ]</p>
        <router-link :to="{path: 'Map', query: {latitude: latitude, longitude: longitude}}">
           <img :src="mapPicUrl" alt="">
        </router-link>
      </div>
      <div class="erweima">
        <img :src="qrcodePicUrl" alt="">
        <span>扫描二维码关注本店</span>
      </div>
      <p class="footer">已经到底了</p>
    </footer>
  </div>
</template>

<script>
var config = require("./../../assets/config.js");
const projectname = config.projectname;
const urls = config.urls;
export default {
  data() {
    return {
      height: "400px",
      overflow: "hidden",
      name: "",
      longitude:'',
      latitude:'',
      areaInfo: "",
      details: "",
      enviroment: "",
      introduction: "",
      telephone: "",
      telephone: "",
      consult: "",
      baseUrl: urls + "/" + projectname + "/",
      qrcodePicUrl: "",
      mapPicUrl: "",
      worktime: "",
      address: "",
      storePhone:"",
      brands: []
    };
  },
  components: { Map },
  methods: {
    lookore(index){
      console.log(index);
      if (this.brands[index].brandlogos.length > 8) {
          this.brands[index].showall = true;
          this.brands[index].con = '收起'
        }else{
          this.brands[index].showall = false
        }
    },
    hidemore(index){
      console.log(index);
      if (this.brands[index].brandlogos.length > 8) {
          this.brands[index].showall = false;
          this.brands[index].con = '查看全部'
        }else{
          this.brands[index].showall = true
        }
    },
  },
  mounted: function() {
    console.log(this.$route.query.id);
    var id = this.$route.query.id;
    var detail = config.api.getmdInfo + "?id=" + id;
    console.log(detail);
    this.$http.get(detail, { id: id }).then(
      response => {
        console.log("***********详情页数据************************")
        console.log(response);
        console.log(response.body.mdinfo.latitude)
        console.log(response.body.mdinfo.longitude)
        this.latitude = response.body.mdinfo.latitude;
        this.longitude = response.body.mdinfo.longitude;
        this.qrcodePicUrl = this.baseUrl + response.body.mdinfo.qrcodePicUrl;
        this.name = response.body.mdinfo.name;
        this.areaInfo = response.body.mdinfo.areaInfo;
        this.details = response.body.mdinfo.details;
        this.enviroment = response.body.mdinfo.enviroment;
        this.introduction = response.body.mdinfo.introduction;
        this.telephone = response.body.mdinfo.telephone;
        this.storePhone = response.body.mdinfo.storePhone;
        this.consult = response.body.mdinfo.consult;
        this.worktime = response.body.mdinfo.worktime;
        this.address = response.body.mdinfo.address;
        var brand = JSON.parse(response.body.mdinfo.brand);
        this.mapPicUrl = this.baseUrl + response.body.mdinfo.mapPicUrl;
        //处理后台返回的map数据
        for (var key in brand) {
          var map = new Object();
          map.title = key;
          map.showall = false;
          map.con = "查看全部";
          map.brandlogos = brand[key];
          this.brands.push(map);
          console.log(map.showall)
        }
        console.log('品牌数据-------------------------')
        //默认显示小于等于8个
        for (var i = 0; i < this.brands.length; i++) {
          console.log(this.brands[i].brandlogos.length);
          if (this.brands[i].brandlogos.length <= 8) {
            this.brands[i].showall = false;
          }
        }
        console.log(this.brands);
      },
      error => {
        console.log(error);
      }
    );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.details{
  background: #f6f6f6;
}
header .logo {
  display: block;
  width: 1.1rem;
  margin: 0 auto;
}

header .title {
  color: #737373;
  font-size: 26px;
  text-align: center;
  padding: 1.2rem 0 0.3rem;
}

header .pass {
  width: 70%;
  margin: 0.25rem auto 0;
  line-height: 30px;
  font-size: 20px;
  text-align: center;
  color: #313131;
}

.content {
  padding: 100px 20px 0;
}

.position {
  text-align: left;
}

.position img {
  width: 30px;
}

.name span {
  color: #434343;
}

.sp1 {
  display: block;
  margin-top: 20px;
  margin-bottom: 4px;
  font-size: 26px;
}

.sp2 {
  margin-top: 20px;
  display: inline-block;
  padding: 0 12px 14px 0;
  font-size: 32px;
  border-bottom: 10px solid #80c43d;
}

.position i {
  display: inline-block;
  font-style: normal;
  font-size: 18px;
  color: #a1a1a1;
  width: auto;
  /* height: 30px; */
  text-align: center;
  /* line-height: 30px; */
  border: 1px solid #959595;
  border-radius: 20px;
  margin-top: 24px;
  padding: 5px 14px;
}

.introduct {
  text-align: left;
}

.intros span {
  font-size: 24px;
  color: #a8a8a8;
  font-weight: bold;
}

.intros {
  border-bottom: 5px solid #8ac944;
  padding-bottom: 23px;
  margin-bottom: 23px;
}

.intros.title {
  margin-bottom: 0;
}

.intros i {
  font-style: normal;
  color: #333;
  font-size: 22px;
}

.passage {
  font-size: 20px;
  line-height: 36px;
}

.icon {
  width: 40px;
  margin-top: 102px;
  margin-bottom: 20px;
}

.brand .title {
  flex: 2;
  font-size: 24px;
  color: #a7a7a7;
}

.brand li {
  display: flex;
  flex: 10;
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 34px;
  padding-top: 30px;
}

.brand li:last-child {
  border-bottom: 0;
}

.brandlogos {
  flex: 8;
  text-align: right;
}

.brand .imgs {
  width: 16%;
  height: 75px;
  padding: 3%;
  margin-right: 4%;
}

.brand img:nth-of-type(4n) {
  margin-right: 0;
}

.more {
  display: inline-block;
  color: #a7a7a7;
  font-size: 22px;
  margin-top: 55px;
}

.endlogo {
  margin-top: 170px;
  width: 100px;
}

.contact p:nth-of-type(1) {
  margin-top: 20px;
}

.contact p {
  text-align: center;
  font-size: 18px;
  line-height: 46px;
}

.contact p span {
  color: #959595;
}

.contact p i {
  font-style: normal;
  color: #000;
}

.erweima img {
  width: 150px;
  margin-top: 80px;
  border: 10px solid #eeeeee;
}

.erweima span {
  display: block;
  text-align: center;
  color: #535353;
  font-size: 20px;
  margin-top: 25px;
}

.footer {
  text-align: center;
  color: #939393;
  font-size: 18px;
  margin-top: 200px;
  padding-bottom: 86px;
}

.map p {
  margin-top: 100px;
  text-align: center;
  color: #80c43d;
  font-size: 20px;
}
.map img {
  display: block;
  margin: 20px auto 0;
  width: 410px;
  height: 410px;
  border: 10px solid #80c43d;
}
.showall{
  height: auto;
  overflow: visible;
}
.showsome{
  height: 3.25rem;
  overflow: hidden;
}
.nav-logo{
  vertical-align: middle;
  width: 8px;
  height: 15px;
  margin-top: -2px;
}
</style>



