<template>
         <div>
                <div class="header-top">
                        <div class="head-title">
                              报名表
                        </div>
                        
                           <form action="" method="post">
                               <div class="form-list">
                                        <div class="form-color">
                                            <p class="form-ops">
                                                <label>姓名</label> 
                                                <input type="text"  placeholder="请输入您的姓名" @blur="checkname"  v-model="name" ref="input"/>
                                            </p>
                                            <p class="form-ops">
                                                <label>企业名称</label> 
                                                <input type="text" name="fname" placeholder="请输入项目名称" @blur="checktext"  v-model="nametext"/>
                                              </p>
                                            <p class="form-ops checkeds">
                                                <label class="labels">企业内部</label> 
                                                <input type="checkbox" name="btn" id="btn1">
                                                <label class="labels-rigjt">企业/商户</label>
                                                <input type="checkbox" name="btn" id="btn1">
                                            </p>
                                            <p class="form-ops">
                                                <label>联系电话</label>
                                                <input type="text" name="fname" placeholder="请输入您的电话" @blur="checkphone" v-model="phone"/>
                                            </p>
                                            <p class="form-text">
                                                <label>个人简介</label> 
                                                <textarea placeholder="字数不超过105个字符..."></textarea>
                                            </p>
                                        </div>
                                </div>
                                    <input type="submit" value="立即提交" class="btns" />
                            </form>

                       

                </div>


         </div>
</template>

<script>
import { Toast } from 'mint-ui';

export default {
  data (){
      return{
         name:'',
         nametext:'',
         phone:'',
      }
  },
  methods:{
            checkname(){
              console.log("***********************")
              console.log(this.name)
                if(this.name==""){
                    Toast('用户名不能为空');
                }
            },
            checktext(){
                 if(this.nametext==""){
                    Toast('企业名称不能为空');
                }
            },
             //验证手机号码部分
            checkphone(){
                var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
                if(this.phone==''){
                    Toast('请输入手机号码');
                }else if(!reg.test(this.phone)){
                     Toast('手机格式不正确');
                     this.phone='';
                }
      },
   mounted() {
            this.$refs['input'].focus();
      }
  }
};
</script>

<style scoped>
.header-top {
  width: 590px;
  margin: 0 auto;
}
input::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #a9a9a9;
}
.head-title {
  width: 100%;
  height: 60px;
  line-height: 60px;
  text-align: center;
  font-size: 26px;
  color: #f6f6f6;
  background: #f5ab00;
  margin-top: 75px;
}
.form-list {
  width: 100%;
  background: #ffffff;
}
.form-color {
  background: #ffffff;
  margin: 0 20px;
}
.form-ops {
  width: 100%;
  height: 80px;
  line-height: 80px;
  border-bottom: solid 1px #eeeeee;
}
.form-ops label {
  width: 105px;
  display: inline-block;
  font-size: 26px;
  color: #474747;
  margin-right: 50px;
}
.form-ops input {
  font-size: 24px;
  line-height: 78px;
  border: none;
}
.checkeds {
  display: flex;
  align-items: center;
}

.form-text {
  display: flex;
  margin-top: 15px;
}
.form-text label {
  width: 105px;
  display: inline-block;
  font-size: 26px;
  color: #474747;
  margin-right: 47px;
  margin-top: 10px;
}
.labels {
  margin-right: 30px !important;
  margin-top: 2px;
}
.labels-rigjt {
  margin-top: 5px;
  width: 120px !important;
  margin-right: 20px !important;
  margin-left: 60px;
}
.form-text textarea {
  width: 380px;
  height: 230px;
  font-size: 20px;
  background: #f8f7f7;
  line-height: 40px;
  padding-left: 10px;
  border: none;
  margin-bottom: 20px;
}
.btns {
  width: 100%;
  height: 75px;
  line-height: 75px;
  text-align: center;
  background: #f5ab00;
  border-radius: 50px;
  border: none;
  margin-top: 70px;
  font-size: 30px;
  color: #ffffff;
}

input[type="checkbox"] {
  width: 25px;
  height: 25px;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  line-height: 15px;
  position: relative;
}
input[type="checkbox"]::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #fff;
  width: 100%;
  height: 100%;
  border: 1px solid #a0a0a0;
}
input[type="checkbox"]:checked::before {
  content: "\2713";
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  border: 1px solid #a0a0a0;
  color: #f5ab00;
  font-size: 45px;
  font-weight: 900;
}
</style>



