import Vue from 'vue'
import Router from 'vue-router'
import Index from '../page/indexBtn/index.vue'
import Coopindex from '../page/cooperation/coopindex.vue'
import Download from '../page/cooperation/aboutlink/download'
import Storesinfo from '../page/storesinfo/index.vue'
import Storedetail from '../page/storesinfo/store_detail.vue'
import Study from './../page/study/study.vue'
import Course from './../page/study/course/course'
import Attract from './../page/study/attract/attract'
import Logins from './../page/study/logins/logins'
import Recruit from './../page/recruit/recruit.vue'
import Talk from './../page/talk/talk.vue'
import Detailpage from './../page/talk/detail_page'
import Details from './../page/talk/details'
import Sight from './../page/sight/sight'
import Map from "./../components/map.vue"
import Setupstore from './../page/cooperation/setUpStore/setUpstore'
import Contact from './../page/cooperation/contact/contact'
import Operation from './../page/cooperation/operation/operation'


Vue.use(Router)
export default new Router({
  routes: [
  {path: "/",redirect:"/Index"},
  {path: "/Index",component:Index},
  {path: "/Storesinfo",component:Storesinfo},
  {path: "/Storedetail",component:Storedetail},
  {path:"/Map",component:Map},
  {path: "/Coopindex",component:Coopindex},
  {path: "/Download",component:Download},
  {path: "/Sight",component:Sight},
  {path: "/Study",component:Study},
  {path: "/Course",component:Course},
  {path: "/Attract",component:Attract},
  {path: "/Logins",component:Logins},
  {path: "/Recruit",component:Recruit},
  {path: "/Talk",component:Talk},
  {path: "/Detailpage",component:Detailpage},
  {path: "/Details",component:Details},
  {path: "/Setupstore",component:Setupstore},
  {path: "/Contact",component:Contact},
  {path: "/Operation",component:Operation}
]
})



