<template>
  <div>
    <div class="top">
      <img :src="title_image">
    </div>
    <div class="content">
      <div class="list" v-for="(item,index) in list">
        <p>{{item.title}}</p>
        <ul>
          <li v-for="(i,index) in item.descriptions">{{i.content}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  var config = require("./../../../assets/config.js");
  const getinfo = config.api.opegetinfo;
  export default {
    data() {
      return {
        list: [],
        headlog: '',
        headtext: '',
        title_image:require("../../../assets/opera_title.png")
      }
    },
    mounted() {
      this.$http.get(getinfo).then(
        response => {
          console.log(response);
          this.list = JSON.parse(response.body.info.content);
        },
        error => {
          console.log(error);
        }
      )
    }
  }

</script>
<style scoped>
  .top {
    padding: 125px 0 55px 0;
    border-bottom: 1px solid #959595;
  }

  .top>img {
    width: 209px;
    height: 109px;
  }

  .content {
    padding: 25px 0 125px 0;
  }

  .content>div:nth-of-type(2n) {
    background: #eeeeee;
  }

  .content>.list {
    padding: 25px 82px;
    border-bottom: 1px solid #c8c8c8;
  }

  .content>.list>p {
    border-left: 10px solid #ffc000;
    padding-left: 15px;
    font-size: 28px;
    color: #ffc000;
    font-weight: bold;
  }

  .list>ul {
    margin-top: 10px;
    margin-left: 20px;
  }

  .list>ul li {
    list-style-type: disc;
    text-align: left;
    line-height: 50px;
    font-size: 22px;
    color: #7a7a7a;
  }

</style>



