<template>
  <div class="store_info">
    <router-view></router-view>
    <Bannerp :slides="slides"></Bannerp>
    <!-- 切换组件 -->
    <div class="tabs">
        <mt-navbar class="page-part clearfix" v-model="selected">
          <mt-tab-item id="1">北京门店</mt-tab-item>
          <mt-tab-item id="2">外省门店</mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <mt-tab-container v-model="selected">
          <mt-tab-container-item id="1">
              <ul class="tab">
                <li v-for="(item,index) in beijing" :key="index">
                  <router-link :to="{path: 'Storedetail', query: {id: item.id }}">
                    <img class="logo" :src="baseUrl+item.mdLogPic" alt="">
                    <div class="door">
                      <p class="title">{{item.name}}</p>
                      <p class="address">{{item.address}}</p>
                      <p class="tel"><img src="../../assets/tel.png" alt="">{{item.storePhone}}</p>
                    </div>
                    <img class="jt" src="../../assets/st.png" alt="">
                  </router-link>
                </li>
              </ul>
              <footer @click="loadMore">{{loading}}</footer>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
              <ul class="tab">
                <li v-for="(item,index) in waisheng" :key="index">
                  <router-link :to="{path: 'Storedetail', query: {id: item.id }}">
                    <img class="logo" :src="baseUrl+item.mdLogPic" alt="">
                    <div class="door">
                      <p class="title">{{item.name}}</p>
                      <p class="address">{{item.address}}</p>
                      <p class="tel"><img src="../../assets/tel.png" alt="">{{item.storePhone}}</p>
                    </div>
                    <img class="jt" src="../../assets/st.png" alt="">
                  </router-link>
                </li>
              </ul>
              <footer @click="loadMore1">{{loading2}}</footer>
          </mt-tab-container-item>
        </mt-tab-container>
    </div>
  </div>
</template>

<script>
import Bannerp from "./../../components/bannerp";
var config = require("./../../assets/config.js");
const getBanner = config.api.getbanner;
const projectname = config.projectname;
const urls = config.urls;
const getMdlist = config.api.getMdlist;
export default {
  name: "",
  data() {
    return {
      loading: ">>>>点击加载更多<<<<",
      loading2: ">>>>点击加载更多<<<<",
      selected: "1",
      flag: 1,
      flags: 2,
      page: 1,
      pages: 1,
      limit: 4,
      beijing: [],
      baseUrl: urls + "/" + projectname + "/",
      waisheng: [],
      slides: [],
      id: ""
    };
  },
  components: { Bannerp },
  methods: {
    //北京
    loadMore: function() {
      if (this.flag == 1) {
        console.log("000");
        var pageNum = ++this.page;
        console.log(pageNum);
        const getMdlist = config.api.getMdlist;
        this.$http
          .post(getMdlist, { page: pageNum, limit: this.limit, mdArea: "2" })
          .then(
            response => {
              console.log(response);
              console.log(response.body.page.list);
              this.beijing = this.beijing.concat(response.body.page.list);
              console.log(pageNum * this.limit);
              console.log(response.body.page.totalCount);
              if (pageNum * this.limit > response.body.page.totalCount) {
                this.loading = ">>>>加载完成<<<<";
                this.flag = 0;
              }
            },
            error => {
              console.log(error);
            }
          );
      }
    },
    //外省
    loadMore1: function() {
      if (this.flags == 2) {
        console.log("000");
        var pageNum = ++this.pages;
        console.log(pageNum);
        const getMdlist = config.api.getMdlist;
        this.$http
          .post(getMdlist, { page: pageNum, limit: this.limit, mdArea: "1" })
          .then(
            response => {
              console.log(response);
              console.log(response.body.page.list);
              this.waisheng = this.waisheng.concat(response.body.page.list);
              console.log(pageNum * this.limit);
              console.log(response.body.page.totalCount);
              if (pageNum * this.limit > response.body.page.totalCount) {
                this.loading2 = ">>>>加载完成<<<<";
                this.flags = 0;
              }
            },
            error => {
              console.log(error);
            }
          );
      }
    }
  },
  mounted() {
    /**
     * 获取banner轮播图
     */
    this.$http.get(getBanner).then(
      response => {
        console.log("********************************")
        console.log(response);
        this.slides = response.body.banner;
        for (var i = 0; i < this.slides.length; i++) {
          this.slides[i].url =
            urls + "/" + projectname + "/" + this.slides[i].url;
        }
      },
      error => {
        console.log(error);
      }
    );

    /**
     * 获取门店列表,分页 北京
     */
    this.$http
      .post(getMdlist, { page: this.page, limit: "4", mdArea: "2" })
      .then(
        response => {
          this.waisheng = response.body.page.list;
        },
        error => {
          console.log(error);
        }
      );
    /**
     * 获取门店列表,分页 外省
     */
    this.$http
      .post(getMdlist, { page: this.page, limit: "4", mdArea: "1" })
      .then(
        response => {
          console.log(response);
          this.beijing = response.body.page.list;
          console.log(this.beijing);
        },
        error => {
          console.log(error);
        }
      );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.door {
  width: 50%;
}

.tab {
  padding: 0 20px;
}

.tab li {
  position: relative;
  margin-top: 0.35rem;
}

.tab li a {
  display: flex;
  background: #f2f4fd;
  border: 1px solid #bbbbbb;
  padding: 0.2rem;
  text-decoration: none;
}

.tab li .logo {
  width: 2rem;
  height: 2rem;
  margin-right: 26px;
}

.tab li .jt {
  width: 0.2rem;
  height: 0.3rem;
  position: absolute;
  top: 0;
  right: 14px;
  bottom: 0;
  margin: auto;
}

.title {
  color: #3b3b3b;
  font-size: 30px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.address {
  color: #3b3b3b;
  height: 62px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tel {
  color: #bdbdbd;
}

.tel img {
  width: 0.3rem;
  margin-right: 12px;
  vertical-align: middle;
  margin-top: -2px;
}

footer {
  color: #ababab;
  padding: 60px 0 100px;
  font-size: 0.35rem;
}
</style>



