<template>
  <div class="store_info">
    <router-view></router-view>
    <Banner :slides="slides"></Banner>
    <ul class="tab clearfix">
      <li v-for="(item,index) in sight" :key="index" @click="See(item.url)">
        <div class="img-logos">
          <img :src="baseUrl+item.log" alt="">
          <!-- <div :style="{backgroundImage: 'url(' + baseUrl+item.log + ')'}"></div> -->
        </div>
        <p class="title">【{{item.name}}】</p>
        <p class="time">{{item.number}}</p>
      </li>
    </ul>
    <footer @click="loadMore">{{loading}}</footer>
  </div>
</template>

<script>
import Banner from "./../../components/banner";
var config = require("./../../assets/config.js");
const projectname = config.projectname;
const urls = config.urls;
var paperGetbanner = config.api.paperGetbanner;
var paperGetlog = config.api.paperGetlog;
export default {
  name: "",
  data() {
    return {
      loading: ">>>> 点击加载更多 <<<<",
      page: "1",
      limit: "4",
      flag:2,
      baseUrl: urls + "/" + projectname + "/",
      sight: [],
      slides: []
    };
  },
  components: { Banner },
  methods: {
    See(url) {
      window.location.href = url;
    },
    //加载更多期刊
    loadMore: function() {
      if (this.flag == 2) {
        var pageNum = ++this.page;
        this.$http.post(paperGetlog, { page: pageNum, limit: this.limit }).then(
          response => {
            console.log(response);
            this.sight = this.sight.concat(response.body.page.list);
            if (pageNum * this.limit > response.body.page.totalCount) {
              this.loading = ">>>>加载完成<<<<";
              this.flag = 1;
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  },
  mounted: function() {
    /**
     * 获取banner
     */
    this.$http.post(paperGetbanner, { id: 1 }).then(response => {
      console.log(response);
      this.slides = response.body.infolist;
      for (var i = 0; i < this.slides.length; i++) {
        this.slides[i].url = this.baseUrl + this.slides[i].banner;
        this.slides[i].content = this.slides[i].content;
      }
    });

    this.$http
      .post(paperGetlog, { page: this.page, limit: this.limit })
      .then(response => {
        console.log(response);
        this.sight = response.body.page.list;
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tab {
  padding: 40px 20px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.img-logos{
    margin:10px; 
    /* width: 100%; */
    height: 365px;
    overflow: hidden;
    text-align: center;
    /* border: solid 1px; */
}
.img-logos div{
  /* width: 100%;
  height: 0;
  padding-top: 140%;
  overflow: hidden;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover; */

  width: 100%;
  height: 365px;
  background-size: 100% 100%;
  
}
.img-logos img{
  width: 100%;
  height: 100%;
}
.tab li {
  width: 48%;
  height: 460px;
  border: 1px solid #ddd;
  margin-bottom: 20px;
}
.tab li img {
  display: block;
  width: 100%;
  height: 365px;
}
.tab li .title {
  color: #2c2c2c;
  font-size: 20px;
  line-height: 24px;
  margin-top: 20px;
  padding-left: 40px;
  font-weight: 700;
}
.tab li .time {
  color: #9d9d9d;
  font-size: 20px;
  line-height: 24px;
  margin-top: 10px;
  padding-left: 35px;
}
footer {
  color: #ababab;
  padding: 10px 0 100px;
  font-size: 0.35rem;
}
</style>



