<template>
     <div>
          <div class="detail-List">
                 <div class="titles">{{title}}</div>
                <div class="descBox" v-html='desc'></div>
          </div>
     </div>
</template>

<script>
var config = require("./../../assets/config.js");
export default {
    data(){
         return {
            desc:'',
            title:'',
         }
    },
    mounted: function() {
        console.log("*************接受的参数********&&&&&&&&&&&&&&&&&")
        var Ids= this.$route.query.bannerId
        console.log(Ids)
    var detail = config.api.bannerId + "?bannerId=" + Ids;
     this.$http.get(detail).then(
      res => {
          var Htmls=res.body.bannerDetail.content;
          var html= unescape(Htmls)
          console.log(html)
            this.desc=html;
            this.title=res.body.bannerDetail.title;
      },
      error => {
        console.log(error);
      }
    );

  }
}
</script>

<style scoped>
.descBox{
    text-align: left !important;
}
 .detail-List{
     margin: 0 40px;
 }
 .header-tetle{
     
     font-size: 32px;
     color: #525252;
     margin: 70px 20px 55px 20px;
 }
 .header-text{
     font-size: 24px;
     color: #313131;
 }
 .tetle-hend{
     margin-top: 150px;
     margin-bottom: 20px;
     font-size: 22px;
     color: #616161;
 }
 .telte-lists{
     font-size: 22px;
     color: #313131;
 }
 .telte-imgs{
     margin-top: 70px;
     height: 315px;
     background: black;
 }
  .titles{
     margin-top: 70px;
     margin-bottom: 20px;
     text-align: center;
     font-size: 32px;
 }
</style>



