<template>
  <div class="banner">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,index) in slides" :key="index">
        <span  @click="btns(item.id)"><img :src="item.url" alt="" width="100%"></span>
        <!-- <span v-else @click="See(item.content)"><img :src="item.url" alt="" width="100%"></span> -->
      </mt-swipe-item> 
    </mt-swipe>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    slides: {
      type: Array,
      default: []
    }
  },
  data() {
    return {};
  },
  methods: {
    See(content) {
      window.location.href = content;
    },
    btns(id){
      console.log("******跳转路由页面***********")
      console.log(id)
      var id=id;
      this.$router.push({
        path:"/Detailpage",
       query:{//通过query 传递参数
          bannerId:id,
        }
      });
    }

  },

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  display: block;
  height: 100%;
  width: 100%;
}
</style>



