<!-- 页面模版 -->
<template>
  <!--container-->
  <div id="allmap" style="width: 100%; position: absolute;left: 0;right: 0;top: 0;bottom: 0;"></div>
  <!--container end-->
</template>
<script>
import BMap from "BMap";
export default {
  data() {
    return {};
  },
  mounted() {
    //经度 latitude
    console.log(this.$route.query.latitude);
    console.log(this.$route.query.longitude);
    //经度 longitude
    this.ready();
  },
  methods: {
    ready: function() {
      // 百度地图API功能
      var map = new BMap.Map("allmap");
      var point = new BMap.Point(this.$route.query.longitude, this.$route.query.latitude);
      map.centerAndZoom(point, 15);
      var opts = {
        position: point, // 指定文本标注所在的地理位置
        offset: new BMap.Size(30, -30) //设置文本偏移量
      };
      var label = new BMap.Label("北京华联购物中心", opts); // 创建文本标注对象
      label.setStyle({
        color: "red",
        fontSize: "18px",
        height: "30px",
        lineHeight: "30px",
        fontFamily: "微软雅黑"
      });
      map.addOverlay(label);
    }
  }
};
</script>
<style>
/* 去掉地图左下角的百度LOGO */

.anchorBL {
  display: none;
}
</style>



