<template>
  <div class="banner">
    <mt-swipe :auto="4000">
      <mt-swipe-item v-for="(item,index) in slides" :key="index">

        <span @click="See(item.content)"><img :src="item.url" alt="" width="100%"></span>
      </mt-swipe-item> 
    </mt-swipe>
  </div>
</template>
<script>
export default {
  name: "",
  props: {
    slides: {
      type: Array,
      default: []
    }
  },
  data() {
    return {};
  },
  methods: {
    See(content) {
      window.location.href = content;
    },
  },

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img {
  display: block;
  height: 100%;
  width: 100%;
}
</style>



