<template>
  <div class="openshop">
    <router-view></router-view>
    <!-- 日常运营 -->
    <router-link to="Operation">
      <div class="rcyy"></div>
    </router-link>
    <!-- 开店流程 -->
    <router-link to="Setupstore">
      <div class="kdlc"></div>
    </router-link>
    <!-- 联系我们 -->
    <router-link to="Contact">
      <div class="lxwm"></div>
    </router-link>
    <!-- 相关下载 -->
    <router-link to="Download">
      <div class="xgxz"></div>
    </router-link>
    
  </div>
</template>

<script>
  export default {
    name: "Coopindex",
    data() {
      return {};
    },
    methods: {

    }
  };

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  a {
    color: #42b983;
  }

  .openshop {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: url(../../assets/bg.jpg) no-repeat;
    background-size: 100% 100%;
  }

  .openshop .rcyy {
    position: absolute;
    left: 2rem;
    top: 2.2rem;
    width: 3.41rem;
    height: 1.7rem;
    /* border: 1px solid red; */
    transform: rotate(-30deg);
    -moz-transform: rotate(-30deg);
    /* Firefox */
    -webkit-transform: rotate(-30deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(-30deg);
    /* Opera */
  }

  .openshop .kdlc {
    position: absolute;
    right: 0.5rem;
    top: 5rem;
    width: 3.41rem;
    height: 1.9rem;
    transform: rotate(31deg);
    -moz-transform: rotate(31deg);
    /* Firefox */
    -webkit-transform: rotate(31deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(31deg);
    /* Opera */
    /* border: 1px solid red; */
  }

  .openshop .lxwm {
    position: absolute;
    left: 1.2rem;
    top: 5.8rem;
    width: 3.41rem;
    height: 2rem;
    transform: rotate(-29deg);
    -moz-transform: rotate(-29deg);
    /* Firefox */
    -webkit-transform: rotate(-29deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(-29deg);
    /* Opera */
    /* border: 1px solid red; */
  }

  .openshop .xgxz {
    position: absolute;
    right: 1rem;
    bottom: 1rem;
    width: 3.41rem;
    height: 1.7rem;
    transform: rotate(-35deg);
    -moz-transform: rotate(-35deg);
    /* Firefox */
    -webkit-transform: rotate(-35deg);
    /* Safari 和 Chrome */
    -o-transform: rotate(-35deg);
    /* Opera */
    /* border: 1px solid red; */
    z-index: 10000000000;
  }

</style>



