<template>
  <div class="container">
    <!-- 上半部分 -->
    <div class="top">
      <img :src="title_image">
      <img :src="baseUrl+image">
      <img src="../../../assets/step.jpg">
      <img src="../../../assets/triangle.jpg">
    </div>

    <!-- 内容区域 -->
    <div class="content">
      <div class="list clearfix" v-for="(item,index) in list" :key="index">
        <p class="icon">{{index + 1}}</p>
        <div class="line">
          <div class="corner"></div>
          <div class="title">
            <img :src="baseUrl+item.log">
            <p>{{item.title}}</p>
          </div>
          <p>{{item.text}}</p>
        </div>
      </div>
    </div>
    </div>


  <!-- </div> -->
</template>
<script>
  var config = require("./../../../assets/config.js");
  const getinfo = config.api.getinfo;
  const projectname = config.projectname;
  const urls = config.urls;
  export default {
    data() {
      return {
        baseUrl: urls + "/" + projectname + "/",
        list:[],
        image:'',
        title_image:require("../../../assets/set_title.png")
      }
    },
    mounted() {
      this.$http.get(getinfo).then(
        response => {
          console.log(response);
          this.list = JSON.parse(response.body.info.context);
          this.image = response.body.info.log;
          console.log(this.list)
        },
        error => {
          console.log(error);
        }
      )
    }
  }

</script>
<style scoped>
  .top {
    padding: 92px 45px 67px 45px;
    text-align: center;
  }

  .top>img:nth-child(1) {
    width: 270px;
    height: 170px;
  }

  .top>img:nth-child(2) {
    margin-top: 77px;
    width: 430px;
    height: 340px;
  }

  .top>img:nth-child(3) {
    margin-top: 101px;
    width: 552px;
    height: 24px;
  }

  .top>img:nth-child(4) {
    margin-top: 8px;
    width: 22px;
    height: 12px;
  }

  .content {
    padding: 0 70px;
    margin-bottom: 157px;
  }

  .list {
    position: relative;
    display: block;
    width: 500px;
    border-left: .05rem dotted #b5b5b5;
    margin-bottom: 25px;
  }

  .list>.icon {
    position: absolute;
    left: -33px;
    width: 60px;
    color: #515352;
    font-size: 33px;
    text-align: center;
  }

  .content>div:first-child>p {
    top: -25px;
    line-height: 70px;
    height: 70px;
    background: url('../../../assets/first.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .content>div:not(:first-child)>p {
    top: -35px;
    height: 86px;
    line-height: 85px;
    background: url('../../../assets/middle.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .content>div:last-child>p {
    top: -35px;
    height: 185px;
    font-size: 0 !important;
    overflow: hidden;
    background: url('../../../assets/last.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .content>.list>.line {
    position: relative;
    width: 368px;
    float: right;
    padding: 25px 30px 42px 33px;
    border-radius: 0 20px 20px 20px;
  }

  .content>div:nth-of-type(2n+1)>div {
    background: #e8e8e8;
  }

  .content>div:nth-of-type(2n+1)>div>.corner {
    background: url('../../../assets/corner_gray.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .content>div:nth-of-type(2n)>div>.corner {
    background: url('../../../assets/corner_purple.jpg') no-repeat;
    background-size: 100% 100%;
  }

  .content>div:nth-of-type(2n)>div {
    background: #ece9f5;
  }

  .content>.list>.line>.corner {
    width: 14px;
    height: 14px;
    position: absolute;
    top: 0;
    left: -14px;
  }

  .content>.list>.line>.title {
    position: relative;
  }

  .content>.list>.line>.title>img {
    width: 70px;
    height: 75px;
    position: absolute;
    top: -40px;
    left: -10px;
  }

  .content>.list>.line>.title>p {
    float: left;
    margin-left: 23%;
    font-size: 30px;
    padding-bottom: 8px;
    color: #3b3b3b;
    font-weight: bold;
    border-bottom: 1px solid black;
  }

  .content>.list>.line>p {
    margin-top: 20%;
    font-size: 22px;
    color: #5c5c5c;
    line-height: 44px;
  }

</style>



