<template>
  <div class="openshop">
    <router-view></router-view>
    <!-- 岗位介绍 -->
    <router-link to="">
      <div class="rcyy" @click="openBtn()"></div>
    </router-link>
    <!-- 全职招聘  -->
    <router-link to="">
      <div class="kdlc" @click="openBtn()"></div>
    </router-link>
    <!-- 合作会员制度 -->
    <router-link to="">
      <div class="lxwm" @click="openBtn()"></div>
    </router-link>
     <!-- 更多线下服务 -->
    <router-link to="">
      <div class="service" @click="openBtn()"></div>
    </router-link>
    <!-- 应届生招聘 -->
    <router-link to="">
      <div class="xgxz" @click="openBtn()"></div>
    </router-link>
    <!-- 应届生招聘 -->
    <router-link to="">
      <div class="recruit" @click="openBtn()"></div>
    </router-link>
    
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: "Recruit",
  data() {
    return {};
  },
  methods: {
    openBtn:function(){
        Toast({
        message: '开发中，敬请期待',
        position: 'center',
        duration: 3000
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}

a {
  color: #42b983;
text-decoration:none; 
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}

.openshop {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: url(../../assets/recruit.png) no-repeat;
  background-size: 100% 100%;
}

.openshop .rcyy {
  position: absolute;
  left: 1rem;
  top: 2.2rem;
  width: 3.41rem;
  height: 1.7rem;
  /* border: 1px solid red; */
  transform: rotate(30deg);
  -moz-transform: rotate(30deg);
  /* Firefox */
  -webkit-transform: rotate(30deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(30deg);
  /* Opera */
}

.openshop .kdlc {
  position: absolute;
  right: 0.5rem;
  top: 3rem;
  width: 3.41rem;
  height: 1.9rem;
  transform: rotate(31deg);
  -moz-transform: rotate(31deg);
  /* Firefox */
  -webkit-transform: rotate(31deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(31deg);
  /* Opera */
  /* border: 1px solid red; */
}

.openshop .lxwm {
  position: absolute;
  left: 1.2rem;
  top: 4rem;
  width: 3.41rem;
  height: 2rem;
  transform: rotate(29deg);
  -moz-transform: rotate(29deg);
  /* Firefox */
  -webkit-transform: rotate(29deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(29deg);
  /* Opera */
  /* border: 1px solid red; */
}
.openshop .service {
  position: absolute;
  left: 1rem;
  top: 6rem;
  width: 3.41rem;
  height: 2rem;
  transform: rotate(29deg);
  -moz-transform: rotate(29deg);
  /* Firefox */
  -webkit-transform: rotate(29deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(29deg);
  /* Opera */
  /* border: 1px solid red; */
}
.openshop .xgxz {
  position: absolute;
  right: 1rem;
  top: 5.5rem;
  width: 3.41rem;
  height: 1.7rem;
  transform: rotate(35deg);
  -moz-transform: rotate(35deg);
  /* Firefox */
  -webkit-transform: rotate(35deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(35deg);
  /* Opera */
  /* border: 1px solid red; */
  z-index: 10000000000;
}
.openshop .recruit {
  position: absolute;
  right: 0.7rem;
  top: 8.5rem;
  width: 3.41rem;
  height: 1.7rem;
  transform: rotate(35deg);
  -moz-transform: rotate(35deg);
  /* Firefox */
  -webkit-transform: rotate(35deg);
  /* Safari 和 Chrome */
  -o-transform: rotate(35deg);
  /* Opera */
  /* border: 1px solid red; */
  z-index: 10000000000;
}
</style>



