<template>
  <div class="store_info">
    <router-view></router-view>
    <Banners :slides="slides"></Banners>
    <ul class="tab">
      <li v-for="(item,index) in talk" :key="index">
          <router-link :to="{path: 'Details', query: {id: item.id,texts: item.introduction}}" style="display: flex;">
              <div class="talk-logos">
                    <img class="logo" :src="baseUrl+item.log" alt="">
              </div>
              <div class="door">
                <p class="tel">{{item.title}}</p>
                <p class="address">{{item.introduction}}</p>
                <p class="title">{{item.brand}}</p>
              </div>
              <img class="jt" src="../../assets/st.png" alt="">
          </router-link>
      </li>
    </ul>
    <footer @click="loadMore">{{loading}}</footer>
  </div>
</template>

<script>
import Banners from "./../../components/banners";
var config = require("./../../assets/config.js");
const interviewGetbanner = config.api.interviewGetbanner;
const interviewGetlog = config.api.interviewGetlog;
const projectname = config.projectname;
const urls = config.urls;
export default {
  name: "",
  data() {
    return {
      loading: ">>>> 点击加载更多 <<<<",
      page: "1",
      limit: "4",
      flag: 2,
      baseUrl: urls + "/" + projectname + "/",
      talk: [],
      slides: []
    };
  },
  components: { Banners },
  methods: {
    // See(url) {
    //   window.location.href = url;
    // },
    //加载更多期刊
    loadMore: function() {
      if (this.flag == 2) {
        var pageNum = ++this.page;
        this.$http
          .post(interviewGetlog, { page: pageNum, limit: this.limit })
          .then(
            response => {
              console.log(response);
              this.talk = this.talk.concat(response.body.page.list);
              if (pageNum * this.limit > response.body.page.totalCount) {
                this.loading = ">>>>加载完成<<<<";
                this.flag = 1;
              }
            },
            error => {
              console.log(error);
            }
          );
      }
    }
  },
  mounted: function() {
    this.$http.post(interviewGetbanner, { id: 1 }).then(
      response => {
        console.log("返回的数据______________________")
        console.log(response);
        this.slides = response.body.infolist;
        for (var i = 0; i < this.slides.length; i++) {
          this.slides[i].url = this.baseUrl + this.slides[i].banner;
        }
      },
      error => {
        console.log(error);
      }
    );
    this.$http
      .post(interviewGetlog, { page: this.page, limit: this.limit })
      .then(response => {
        // console.log(response);
        this.talk = response.body.page.list;
        // var htmls=decodeURI(response.body.page.list[0].content)
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a{
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-user-select: none;
    -moz-user-focus: none;
    -moz-user-select: none;
}
.door {
  width: 340px;
}

.tab {
  padding: 0 20px;
}

.tab li {
  position: relative;
  display: flex;
  background: #f2f4fd;
  border: 1px solid #bbbbbb;
  padding: 0.2rem;
  margin-top: 0.35rem;
}

.talk-logos img{
    width: 180px;
  height: 140px;
  margin-right: 26px;
}


.tab li .jt {
  width: 0.2rem;
  height: 0.3rem;
  position: absolute;
  top: 0;
  right: 14px;
  bottom: 0;
  margin: auto;
}

.title {
  color: #3b3b3b;
  font-size: 26px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.address {
  color: #3b3b3b;
  font-size: 22px;
  height: 62px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.tel {
  color: #9d9d9d;
  font-size: 20px;
}

.tel img {
  width: 0.3rem;
  margin-right: 12px;
  vertical-align: middle;
}

footer {
  color: #ababab;
  padding: 100px 0 50px;
  font-size: 0.35rem;
}
</style>



