<template>
    <!-- 相关下载 -->
    <div class="xgxz" >
        <p class="header">BHG Mall 系列APP相关下载</p>
        <ul class="down">
            <li v-for="(item, index) in list" :key="index">
                <img :src="baseUrl + item.log" alt="">
                <p>
                  <span class="title">{{item.name}}</span>
                  <i>{{item.info}}</i>
                </p>
                <div class="btns">
                    <span @click="See(item.ios)">iphone</span>
                    <span @click="See(item.android)">Android</span>
                </div>
            </li>
        </ul>
        <footer @click="loadMore">{{loading}}</footer>
    </div>
</template>

<script>
var config = require("./../../../assets/config.js");
const projectname = config.projectname;
const urls = config.urls;
const getapplist = config.api.getapplist;
console.log(urls)
export default {
  name: "Download",
  data() {
    return {
      loading: ">>>> 点击加载更多 <<<<",
      page: "1",
      limit: "8",
      flag: 2,
      baseUrl: urls + "/" + projectname + "/",
      list: []
    };
  },
  methods: {
    See(url) {
      window.location.href = url;
    },
    //加载更多期刊
    loadMore: function() {
      if (this.flag == 2) {
        var pageNum = ++this.page;
        this.$http.post(getapplist, { page: pageNum, limit: this.limit }).then(
          response => {
            console.log(response);
            this.list = this.list.concat(response.body.page.list);
            if (pageNum * this.limit > response.body.page.totalCount) {
              this.loading = ">>>>加载完成<<<<";
              this.flag = 1;
            }
          },
          error => {
            console.log(error);
          }
        );
      }
    }
  },
  mounted: function() {
    this.$http
      .post(getapplist, { page: this.page, limit: this.limit })
      .then(response => {
        console.log(response);
        this.list = response.body.page.list;
      },error=>{
        console.log(error)
      });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.xgxz {
  position: relative;
}
footer {
  text-align: center;
  color: #939393;
  padding: 50px 0 100px;
  font-size: 0.3rem;
}
.title {
  display: block;
  margin-top: 20px;
}
.header {
  width: 100%;
  height:60px;
  line-height: 60px;
  font-size: 21px;
  color: #fff;
  background: url(../../../assets/header_bg.png) no-repeat;
  background-size: 100%;
  text-align: center;
}
.down li {
  display: flex;
  padding: 0.3rem 0.45rem;
  border-bottom: 1px solid #ccc;
}
.down li img {
  width: 80px;
  height: 80px;
  border-radius: 10px;
  margin-top: 10px;
}
.down p {
  flex: 3;
  font-size: 0.3rem;
  line-height: 1rem;
  margin-left: 30px;
}
.down p span {
  display: block;
  line-height: 0.4rem;
  color: #2c2c2c;
  font-size: 0.3rem;
  font-weight: bold;
}
.down p i {
  display: block;
  font-style: normal;
  color: #9d9d9d;
  height: 0.65rem;
  font-size: 0.25rem;
  line-height: 0.35rem;
  overflow: hidden;
}
.btns {
  flex: 2;
  margin-left: 78px;
}
.btns span {
  display: block;
  height: 0.6rem;
  line-height: 0.6rem;
  color: #8ca2ff;
  border: 1px solid #8ca2ff;
  border-radius: 30px;
  text-decoration: none;
}
.btns span:nth-of-type(1) {
  color: #5260b3;
  border: 1px solid #5260b3;
  margin-bottom: 20px;
}
.down li:nth-of-type(2n+1){
  background:#f0f0f0;
}
</style>



