<template>
     <div>
          <div class="detail-List">
                 <div class="titles">{{title}}</div>
                 <div class="textss">{{texts}}</div>
                <div class="descBox" id="apps"></div>
          </div>
     </div>
</template>

<script>
var config = require("./../../assets/config.js");
export default {
    data(){
         return {
            desc:'',
            title:'',
            texts:''
            // list:'<p>&nbsp &nbsp &nbsp &nbsp &nbsp &nbsp 噶哈哈哈哈</p>'
         }
    },
    mounted: function() {

        console.log("*************接受的参数********&&&&&&&&&&&&&&&&&")
        var Ids= this.$route.query.id
        console.log(Ids)
    var detail = config.api.bannerDetails + "?id=" + Ids;
     this.$http.get(detail).then(
      res => {
          console.log(res.body.hlgzhInterviewEntity.introduction)
          var Htmls=res.body.hlgzhInterviewEntity.content;
          console.log("*************富文本转化格式**********************")
          console.log(Htmls);
        var htmlp= unescape(Htmls)
          console.log("******************")
          console.log(htmlp)
          var temp =document.getElementById("apps") 
            temp.innerHTML = htmlp; 
            this.title=res.body.hlgzhInterviewEntity.title;
            if(this.$route.query.texts==undefined){
              this.texts=res.body.hlgzhInterviewEntity.introduction;
            }else{
              this.texts=this.$route.query.texts  
            }
      },
      error => {
        console.log(error);
      }
    );

  }
}
</script>

<style scoped>
.descBox{
  text-align: left !important;
}
 .detail-List{
     margin: 0 40px;
 }
 .header-tetle{
     
     font-size: 32px;
     color: #525252;
     margin: 70px 20px 55px 20px;
 }
 .header-text{
     font-size: 24px;
     color: #313131;
 }
 .tetle-hend{
     margin-top: 150px;
     margin-bottom: 20px;
     font-size: 22px;
     color: #616161;
 }
 .telte-lists{
     font-size: 22px;
     color: #313131;
 }
 .telte-imgs{
     margin-top: 70px;
     height: 315px;
     background: black;
 }
 .titles{
     margin-top: 70px;
     margin-bottom: 60px;
     text-align: center;
     font-size: 32px;
 }
 .textss{
     text-align: center;
     font-size: 24px;
     color: #313131;
     margin-bottom: 40px;
 }
</style>


